import React from "react";
import { Link, withRouter } from "react-router-dom";

import { formatDate, API_URL, getStorageItem } from "../config/config";
import Api from "../config/Api";

import Title from "../components/Title";
import Loading from "../components/Loading";
import Banner from "../components/Banner";

import "../styles/news.css";

class News extends React.Component {

    state = {
        all: true,
        type: 1,
        articles: [],

        loading: false,

        banner: false
    }

    constructor() {
        super();

        this.loadData = this.loadData.bind(this);
        this.showBanner = this.showBanner.bind(this);
        this.closeBanner = this.closeBanner.bind(this);
    }

    async loadData() {
        this.setState({ loading: true });

        if (this.state.all) {
            const blogs1 = await Api.getPosts({ filters: { type: 1 } });
            const blogs2 = await Api.getPosts({ filters: { type: 2 } });
            const blogs3 = await Api.getPosts({ filters: { type: 3 } });
            const blogs4 = await Api.getPosts({ filters: { type: 4 } });
            const blogs5 = await Api.getPosts({ filters: { type: 5 } });

            const blogs = blogs1.blogs.concat(blogs2.blogs, blogs3.blogs, blogs4.blogs, blogs5.blogs);
            this.setState({ loading: false, articles: blogs });
        } else {
            const filters = {
                filters: {
                    type: this.state.type
                }
            }

            const blogs = await Api.getPosts(filters);

            if (blogs.blogs) {
                this.setState({ loading: false, articles: blogs.blogs });
            }
        }
    }

    showBanner() {
        this.setState({ banner: true });
    }

    closeBanner() {
        this.setState({ banner: false });
    }

    componentDidMount() {
        this.loadData();
    }

    render() {
        return(
            <div className="screen" id="news">
                <Title title="Novinky TerraMia" image="title-background-14.jpg" />

                <div className="content" style={this.state.articles.length === 0 ? { alignItems: "center" } : null}>
                    <div className="menu-panel">
                        <div className="title">Kategórie</div>

                        <div className="menu">
                            <div className="item" onClick={() => this.setState({ all: true }, () => this.loadData())}>Novinky<ion-icon name="chevron-forward-outline"></ion-icon></div>
                            <div className="item" onClick={() => this.setState({ type: 1, all: false }, () => this.loadData())}>Udalosti<ion-icon name="chevron-forward-outline"></ion-icon></div>
                            <div className="item" onClick={() => this.setState({ type: 2, all: false }, () => this.loadData())}>Akcie<ion-icon name="chevron-forward-outline"></ion-icon></div>
                            <div className="item" onClick={() => this.setState({ type: 3, all: false }, () => this.loadData())}>Kurzy<ion-icon name="chevron-forward-outline"></ion-icon></div>
                            <div className="item" onClick={() => this.setState({ type: 4, all: false }, () => this.loadData())}>Prednášky<ion-icon name="chevron-forward-outline"></ion-icon></div>
                            <div className="item" onClick={() => this.setState({ type: 5, all: false }, () => this.loadData())}>Semináre<ion-icon name="chevron-forward-outline"></ion-icon></div>
                        </div>
                    </div>

                    {this.state.loading ? <div className="list-panel" style={{ display: "flex", flexDirection: "column", alignItems: "center" }}><Loading /></div> : (
                        <div className="list-panel">
                            <div className="list">
                                {this.state.articles.map((article) => <Article article={article} history={this.props.history} showBanner={this.showBanner} />)}
                            </div>

                            {this.state.articles.length === 0 ? <div className="empty-message">Nenašli sa žiadne novinky v danej kategórií</div> : null}
                        </div>
                    )}
                </div>

                {this.state.banner ? (
                    <Banner
                        title="Tento príspevok je zamknutý"
                        text="Ak chcete čítať blog a novinky bez obmedzení, staňte sa členom TerraMia a získajte neobmedzený prístup ku všetkým informáciam."
                        button="Staň sa členom klubu"
                        closeBanner={this.closeBanner}
                    />
                ) : null}
            </div>
        )
    }
}

function Article(props) {
    const article = props.article;
    const src = API_URL + "/uploads/resized/" + article.imagePath

    return(
        <div className="article" onClick={article.locked ? (getStorageItem("token") ? () => props.history.push("/blog/" + article._id) : () => props.showBanner()) : () => props.history.push("/blog/" + article._id)}>
            <img className="image" src={src} />
            <h3 className="title">{article.name}</h3>
            <div className="date">{formatDate(article.date)}</div>
            <p className="text">{article.description}</p>

            <div className="button-filled">Čítaj viac</div>
        </div>
    )
}

export default withRouter(News);